.products {
  display: flex;
  margin: -15px;
  padding: 100px 0;
}

.products > * {
  margin: 15px;
}

@media only screen and (max-width: 991.98px) {
  .products {
    flex-direction: column;
    margin: -25px 0;
    padding: 50px 0;
  }

  .products > * {
    margin: 25px 0;
  }
}